<template>
	<main>
		<div class="wrap">
			<v-headerx></v-headerx>
		<div class="login">
			<ul>
				<li class="active"><a href="" >美团账号登录</a></li>
				<li><a href="">手机验证登录</a></li>
			</ul>
			<form action="">
				<div><input type="text" placeholder="账户名/手机号/Email"></div>
				<div><input type="password"  placeholder="请输入您的密码"></div>
				<button>登录</button>
				<div class="forget"><span><router-link to="/register">立即注册</router-link></span><span><a href="">忘记密码</a></span></div>
			</form>
		</div>
		</div>
	</main>
</template>
<script>
	import headerx from "../components/headerx.vue"
	export default{
		name:"login",
		components:{
			"v-headerx":headerx
		},
		mounted(){
			this.$store.state.title="登陆";
		}
	}
</script>
<style scoped>
	.wrap{
	    position: fixed;
	    top: 0;
	    bottom: 0;
	    height: 100%;
	    width: 100%;
	    background: #f0efed;
	    z-index: 1;
	}
	

	.login ul{
		overflow: hidden;
		text-align: center;
		box-sizing: border-box;
		background: white;
		padding: 0 10px;
		border-bottom: .08rem solid #DDD8CE;
	}
	.login ul li{
		float: left;
		list-style: none;
		width: 50%;
		font-size: .34rem;
	    padding-top: .28rem;
	    padding-bottom: .2rem;
		position: relative;
	}
	.login ul li a{
		display: block;
	    color: #333;
	}
	.login ul li.active::after{
	    content: "";
	    height: 5px;
	    background: #06c1ae;
	    width: 100%;
	    position: absolute;
	    left: 0;
	    margin-top: 7px;
	}
	.login ul li.active a{
		color: #06c1ae;
	}
	.login form{
		width: 100%;
		background: white;
		box-sizing: border-box;
		padding: 0 10px;
	}
	.login form input{
		width: 100%;
		border: none;
		border-bottom: 1px solid #cec8c8e0;
		 height: .6rem;
	    margin: -.15rem 0;
	    text-indent: .1rem;
	    line-height: 1;
	    font-size: 0.3rem;
	    outline: none;
	}
	.login button{
	    display: inline-block;
	    margin: 0;
	    text-align: center;
	    height: .8rem;
	    border-radius: .06rem;
	    color: #fff;
	    border: 0;
	    background-color: #06c1ae;
	    font-size: .38rem;
	    vertical-align: middle;
	    line-height: .6rem;
	    box-sizing: border-box;
	    cursor: pointer;
	    width: 100%;
	}
	.login .forget{
		font-size: .26rem;
		overflow: hidden;
		padding: .1rem 0;
	}
	.login .forget a{
		color: #06c1ae;
	}
	.login .forget span:first-child{
		float: left;
	}
	.login .forget span:last-child{
		float: right;
	}
</style>


